<template>
    <div class="adv-g__cart-dropdown" v-on:click.stop="">
        <div class="adv-g__cart-dropdown__list">
            <template v-for="(coupon, index) in coupons">
                <cart-list-item
                        v-bind:key="coupon.id"
                        v-bind:coupon="coupon"
                        v-on:remove="$emit('remove', coupon)"
                ></cart-list-item>
                <div v-if="index !== (coupons.length-1)"
                     v-bind:key="coupon.id + 'delimiter'"
                     class="adv-g__delimiter"></div>
            </template>
        </div>
        <Btn type="primary"
             v-if="coupons.length"
             class="adv-g__dropdown__download-btn"
             v-on:click="$emit('download')"
        >
            <template v-slot:icon>
                <DownloadSvg class="adv-btn__icon"></DownloadSvg>
            </template>
            <template v-slot:name>
                <span>{{downloadTitle}}</span>
            </template>
        </Btn>
    </div>
</template>

<script>
    import CartListItem from "./CartIListItem";
    import Btn from "../../ui/Button";
    import DownloadSvg from '../../../assets/download.svg';

    export default {
        name: "cart-list",
        components: {Btn, DownloadSvg, CartListItem},
        props: {
            coupons: {
                type: Array,
                default() {
                    return [];
                }
            }
        },
        computed: {
            downloadTitle() {
                return this.coupons[0] ? this.coupons[0].downloadString : '';
            }
        }
    }
</script>

<style>
    .adv-g__cart-dropdown {
        position: absolute;
        top: calc(100% + 10px);
        right: 0;
        width: 340px;
        background: white;
        display: flex;
        flex-direction: column;
        border-radius: 4px;
        box-shadow: 1px 1px 17px -8px rgba(0, 0, 0, 0.75);
        cursor: default;
    }

    .adv-g__cart-dropdown__list {
        max-height: 300px;
        overflow-y: scroll;
        border: 1px solid #c8c8c8;
        border-top-left-radius: 4px;
        border-top-right-radius: 4px;
        border-bottom: 0;
    }

    .adv-g__delimiter {
        border-top: 1px solid #d5d5d5;
        margin-left: 15px;
        margin-right: 15px;
    }

    .adv-g__dropdown__download-btn {
        border-top-left-radius: 0;
        border-top-right-radius: 0;
        padding: 15px;
    }
</style>
