<template>
    <div class="adv-coupon">
        <div class="adv-coupon__body">
            <div class="adv-coupon__img-wrapper">
                <img
                        v-bind:src="coupon.imageLink"
                        class="adv-coupon__img">
            </div>
            <div class="adv-coupon__discount">
                {{coupon.mainDiscountString}}
            </div>
            <div class="adv-coupon__discount-for-header">
                {{coupon.discountForString}}
            </div>
            <div class="adv-coupon__product-title" v-bind:title="title">
                {{title}}
            </div>
            <div>
                <span class="adv-coupon__valid-till-message">{{coupon.actionTimeString}}
                </span>&#32;<span class="adv-coupon__valid-till-date">{{formattedDate}}</span>
            </div>

            <Btn
                    type="secondary_outline"
                    size="sm"
                    class="adv-coupon__info-btn"
                    v-on:click="openInfo">
                <template v-slot:icon>
                    <InfoSvg class="adv-btn__icon"></InfoSvg>
                </template>
            </Btn>
        </div>
        <div class="adv-coupon__actions">
            <Btn type="primary" v-on:click="downloadFile(pdfUrl)">
                <template v-slot:icon>
                    <DownloadSvg class="adv-btn__icon"></DownloadSvg>
                </template>
                <template v-slot:name>
                    <span>{{coupon.downloadString}}</span>
                </template>
            </Btn>
            <Btn
                    v-bind:type="selected ? 'primary_outline' : 'secondary_outline'"
                    v-on:click="toggleSelection"
            >
                <template v-slot:icon>
                    <CartSvg class="adv-btn__icon"></CartSvg>
                </template>
            </Btn>
        </div>
    </div>
</template>

<script>
    import DownloadSvg from '../../assets/download.svg';
    import InfoSvg from '../../assets/baseline-info-24px.svg';
    import CartSvg from '../../assets/cart.svg';
    import Btn from '../ui/Button';
    import { downloadFile } from "../../utils/download-file";

    export default {
        name: "coupon",
        components: {
            DownloadSvg,
            InfoSvg,
            CartSvg,
            Btn
        },
        props: {
            coupon: {
                type: Object,
            },
            selected: {
                type: Boolean,
                default: false,
            }
        },
        computed: {
            formattedDate() {
                return new Date(this.coupon.endDateTime).toLocaleDateString();
            },
            pdfUrl() {
                return `https://digitalsavertest.azurewebsites.net/api/digitalOffers/GetOfferPDF?digitalOfferId=${this.coupon.offerId}&networkId=${this.coupon.networkIds[0]}`;
            },
            title() {
                return this.coupon && this.coupon.productTitles && this.coupon.productTitles[0];
            }
        },
        methods: {
            toggleSelection() {
                this.$emit('selection', this.coupon);
            },
            openInfo() {
                this.$emit('info', this.coupon);
            },
            downloadFile(url) {
                downloadFile(url);
            }
        }
    }
</script>

<style>
    .adv-coupon {
        max-width: 25%;
        min-width: 270px;
        width: 270px;
        border: solid 1px #ddd;
        border-radius: 5px;
        position: relative;
        margin: 20px;
    }

    .adv-coupon__body {
        height: 300px;
        width: 100%;
        padding: 20px 20px 0 20px;
        position: relative;
    }

    .adv-coupon__actions {
        width: 100%;
        padding: 0 20px 20px 20px;
        display: flex;
        justify-content: space-between;
    }

    .adv-coupon__img-wrapper {
        overflow: hidden;
        position: relative;
        height: 135px;
    }

    .adv-coupon__img {
        width: 100%;
        min-width: 100px;
        left: 50%;
        margin-left: -50%;
        top: 50%;
        margin-top: -50%;
        position: absolute;
    }

    .adv-coupon__discount {
        font-weight: bold;
        font-size: 1.6rem;
        line-height: 2.4rem;
        padding-top: 15px;
        height: calc(2.4rem + 15px);
    }

    .adv-coupon__discount-for-header {
        font-size: 0.85rem;
        color: #838383;
        line-height: 1.3rem;
        height: 1.3rem;
    }

    .adv-coupon__product-title {
        font-weight: bold;
        overflow: hidden;
        height: 2.4rem;
        font-size: 0.9rem;
        line-height: 1.2rem;
        text-overflow: ellipsis;
        white-space: nowrap;
    }

    .adv-coupon__valid-till-message {
        font-size: 0.85rem;
        color: #838383;
        line-height: 1.5rem;
    }

    .adv-coupon__valid-till-date {
        font-size: 0.85rem;
        line-height: 1.5rem;
        font-weight: bold;
    }

    .adv-coupon__info-btn {
        position: absolute;
        top: 20px;
        right: 20px;
    }
</style>
