<template>
    <form class="adv-g__login-form" novalidate>
        <div
                class="adv-g__login-form__error"
                v-if="showLoginError"
        >
            Неправильный адрес или пароль
        </div>
        <div
                class="adv-g__login-form__error"
                v-if="showRegistrationError"
        >
            Регистрация не удалась. Попробуйте позже
        </div>
        <label
                v-bind:for="'adv-gallery-login-form-email-' + $root.galleryId"
                class="adv-g__login-form_label"
        >
            Адрес электронной почты *
        </label>
        <input
                v-bind:id="'adv-gallery-login-form-email-' + $root.galleryId"
                class="adv-g__login-form_input"
                type="email"
                autocomplete="username"
                v-model="loginForm.email"
        >
        <label
                v-bind:for="'adv-gallery-login-form-password-' + $root.galleryId"
                class="adv-g__login-form_label"
        >
            Пароль *
        </label>
        <input
                v-bind:id="'adv-gallery-login-form-password-' + $root.galleryId"
                class="adv-g__login-form_input"
                type="password"
                autocomplete="current-password"
                v-model="loginForm.password"
        >
        <div
                class="adv-g__login-form__error"
                v-if="showPasswordLengthError"
        >
            Минимальная длина пароля - 8 символов
        </div>
        <div class="adv-g_login-form__footer">
            <Btn
                    type="primary"
                    v-on:click="onLogin"
            >
                <template v-slot:name>
                    Войти
                </template>
            </Btn>
            <Btn
                    type="link"
                    v-on:click="onRegister"
            >
                <template v-slot:name>
                    Зарегестрироваться
                </template>
            </Btn>
        </div>
    </form>
</template>

<script>
    import Btn from "../../ui/Button";
    import * as http from './../../../utils/http';

    export default {
        name: "LoginForm",
        components: { Btn },
        data() {
            return {
                loginForm: {
                    email: '',
                    password: ''
                },
                showRegistrationError: false,
                showLoginError: false,
                showPasswordLengthError: false,
            }
        },
        methods: {
            onLogin() {
                this.resetErrors();

                if (!this.loginForm.email) {
                    return;
                }

                if (!this.loginForm.password) {
                    return;
                }

                http
                    .post('https://digitalsavertest.azurewebsites.net/api/auth/login', {
                        userName: this.loginForm.email,
                        password: this.loginForm.password
                    })
                    .then(response => JSON.parse(response))
                    .then(({data,isError}) => {
                        this.showLoginError = isError;

                        if (isError) {
                            return;
                        }

                        this.$root.$children[0].onUserLoggedIn(JSON.parse(data), this.loginForm.email);
                    });
            },
            onRegister() {
                this.resetErrors();

                if (!this.loginForm.email) {
                    return;
                }

                if (!this.loginForm.password || this.loginForm.password.length < 8) {
                    this.showPasswordLengthError = true;
                    return;
                }

                http
                    .post('https://digitalsavertest.azurewebsites.net/api/account/RegisterUser', {
                        email: this.loginForm.email,
                        password: this.loginForm.password
                    })
                    .then(response => JSON.parse(response))
                    .then(({isError}) => {
                        if (!isError) {
                            this.onLogin();
                        } else {
                            this.showRegistrationError = true;
                        }
                    });
            },
            resetErrors() {
                this.showRegistrationError = false;
                this.showPasswordLengthError = false;
                this.showLoginError = false;
            },
        }
    }
</script>

<style scoped>
    .adv-g__login-form {
        position: absolute;
        right: 0;
        top: calc(100% + 10px);
        width: 300px;
        background: white;
        border: 1px solid rgb(11, 177, 67);
        border-radius: 4px;
        padding: 20px;
        box-shadow: 1px 1px 17px -8px rgba(0, 0, 0, 0.75);
    }

    .adv-g__login-form_label {
        font-size: 1rem;
        font-weight: 500;
        margin-bottom: 5px;
        display: inline-block;
    }

    .adv-g__login-form_input {
        width: 100%;
        border-radius: 4px;
        border: 1px solid #d5d5d5;
        font-size: 1rem;
        line-height: 2rem;
        margin-bottom: 20px;
        padding-left: 10px;
    }

    .adv-g__login-form__error {
        font-size: 1rem;
        display: inline-block;
        margin-bottom: 5px;
        color: red;
    }

    .adv-g_login-form__footer {
        display: flex;
        justify-content: space-between;
    }
</style>
