<template>
    <div class="adv-gallery">
        <Header>
            <template v-slot:right>
                <cart
                        v-bind:cart="selectedCoupons"
                        v-bind:total-coupons-number="coupons.length"
                        v-on:remove="toggleCouponInCart"
                ></cart>
                <login class="adv-g__header_login"></login>
            </template>
        </Header>
        <div class="adv-g__coupons-list">
            <coupon
                    v-for="coupon in coupons"
                    v-bind:coupon="coupon"
                    v-bind:key="coupon.id"
                    v-bind:selected="cart[coupon.id]"
                    v-on:info="openInfo"
                    v-on:selection="toggleCouponInCart"
            >
            </coupon>
        </div>
        <coupon-information
                v-if="openedCouponInfo"
                v-bind:data="openedCouponInfo"
                v-on:close="closeInfo">
        </coupon-information>
    </div>
</template>

<script>
    import Coupon from './components/business-logic/Coupon';
    import CouponInformation from "./components/business-logic/CouponInformation";
    import Header from "./components/ui/Header";
    import Cart from "./components/business-logic/cart/Cart";
    import Vue from "vue";
    import * as http from './utils/http';
    import Login from "./components/business-logic/login/Login";

    const UserAuthDataToken = 'adv-gallery-user-auth-data';

    export default {
        name: 'app',
        props: {
            galleryId: {
                type: String,
                required: true
            },
        },
        components: {
            Login,
            Cart,
            Header,
            CouponInformation,
            Coupon
        },
        data: () => {
            const userData = JSON.parse(localStorage.getItem(UserAuthDataToken) || '{}');
            const userIsLoggedIn = userData.expires && userData.expires > new Date().getTime();
            const email = userData.email;

            return ({
                coupons: [],
                openedCouponInfo: null,
                cart: {},
                userIsLoggedIn,
                email
            });
        },
        methods: {
            openInfo(coupon) {
                this.openedCouponInfo = coupon;
            },
            closeInfo() {
                this.openedCouponInfo = null;
            },
            toggleCouponInCart(coupon) {
                Vue.set(this.cart, coupon.id, !this.cart[coupon.id]);
            },
            extendCouponWithId(coupon) {
                return {
                    ...coupon,
                    id: `${coupon.offerId}_${coupon.networkIds[0]}`
                };
            },
            onUserLoggedIn(info, email) {
                localStorage.setItem(UserAuthDataToken, JSON.stringify({
                    ...info,
                    expires: new Date().getTime() + (info.expires_in * 1000),
                    email,
                }));
                this.userIsLoggedIn = true;
                this.email = email;
            },
            onUserLoggedOut() {
                localStorage.removeItem(UserAuthDataToken);
                this.userIsLoggedIn = false;
            }
        },
        mounted() {
            http.get(`https://digitalsavertest.azurewebsites.net/api/gallery/Gallery?id=${this.galleryId}`)
                .then(data => {
                    this.coupons = data.galleryItems.map(this.extendCouponWithId);
                });
        },
        computed: {
            selectedCoupons() {
                return this.coupons.filter(coupon => this.cart[coupon.id]);
            }
        }
    }
</script>

